.contList {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  overflow-y: scroll;
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 450px;
}

.emptyList {
  height: auto;
  overflow-y: hidden;
}

.myButton {
  box-shadow: 3px 4px 0px 0px #3dc21b;
  background: linear-gradient(to bottom, #44c767 5%, #5cbf2a 100%);
  background-color: #44c767;
  border-radius: 18px;
  border: 1px solid #18ab29;

  /* display: inline-block; */
  width: max-content;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 17px;
  padding: 7px 25px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  margin-top: 15px;
  margin-right: 0px;
  margin-left: 50px;
}
.myButton:hover {
  background: linear-gradient(to bottom, #5cbf2a 5%, #44c767 100%);
  background-color: #5cbf2a;
}
.myButton:active {
  position: relative;
  top: 1px;
}
.listItem {
  font-size: 48px;
  color: chocolate;
  /* text-align: center; */
  text-align: justify;
}
